* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-family: "open sans", sans-serif;
}
.app {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
main {
  display: flex;
  flex-grow: 1;
}
header {
  flex-basis: 30vh;
  position: relative;
  overflow: hidden;
}
main nav.menu {
  min-width: 250px;
  height: 100%;
  padding: 10px;
}
main section.main {
  flex-grow: 1;
  padding: 10px 20px;
}
footer {
  padding: 15px 0;
  background: #444;
  color: #fefefe;
  text-align: center;
}

.new-article {
  width: 500px;
  margin: 50px;
}
.new-article h3 {
  margin-bottom: 10px;
}
.new-article span {
  display: block;
  font-size: 14px;
  margin-bottom: 10px;
}
.new-article p {
  line-height: 160%;
}


header img {
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nav ul {
  list-style: none;
}
.nav li {
  margin: 20px 0;
}
.nav a {
  display: block;
  text-decoration: none;
  color: #444;
  font-weight: 700;
  font-size: 1.8rem;
  padding: 10px;
  transition: 0.3s;
  border-left: 0px solid transparent;
}
.nav a:hover {
  transform: translateX(20px);
  border-left: 10px solid #444;
}
.nav a.active {
  border-left: 10px solid #444;
  transform: translateX(20px);
}
.nav li:nth-of-type(1) a {
  border-color: lightgreen;
}
.nav li:nth-of-type(2) a {
  border-color: skyblue;
}
.nav li:nth-of-type(3) a {
  border-color: olivedrab;
}
.nav li:nth-of-type(4) a {
  border-color: orangered;
}

.product-list-title {
  margin: 50px 0 0 10px;
}
.product-list {
  margin: 30px 0 0 50px;
}
.product-list ul {
  list-style: square;
}
.product-list ul li {
  margin: 15px 0;
}
.product-list ul li a {
  display: block;
  color: #444;
  text-decoration: none;
  transition: 0.3s;
}
.product-list ul li a:hover {
  color: red;
  transform: translateX(10px);
}

.contact-containers {
  margin: 30px 0 0 50px;
  width: 300px;
}
.contact-containers form {
  display: flex;
  flex-direction: column;
}
.contact-containers textarea {
  display: block;
  width: 300px;
  height: 200px;
  padding: 5px;
  margin-bottom: 20px;
  line-height: 150%;
  font-size: 16px;
  resize: none;
}
.contact-containers button {
  align-self: flex-end;
  display: block;
  padding: 10px 30px;
  border-radius: 20px;
  border: 2px solid #07bb82;
  color: #efefef;
  background: linear-gradient(
    90deg,
    #0b7197,
    #089cdc,
    #07bb82,
    #48b81e,
    #ff9709,
    #c65612
  );
  font-size: 16px;
  background-size: 700%;
  outline: none;
  transition: 0.5s;
  background-position: 0 0;
  -webkit-animation: none;
          animation: none;
}
.contact-containers button:hover {
  cursor: pointer;
  -webkit-animation: buttonAnimation 8s linear infinite alternate;
          animation: buttonAnimation 8s linear infinite alternate;
}

@-webkit-keyframes buttonAnimation {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 100%;
  }
}

@keyframes buttonAnimation {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 100%;
  }
}

.footer-details p {
  margin: 10px 0;
  color: lightgreen;
  text-transform: uppercase;
  font-size: 10px;
}

